import React from 'react';

interface ConnectLogoProps {
  isActive?: boolean;
  onClick?: () => void;
  pendingRequests?: number;
}

const ConnectLogo: React.FC<ConnectLogoProps> = ({ isActive = false, onClick, pendingRequests = 0 }) => {
  return (
    <div
      onClick={onClick}
      style={{
        width: 48,
        height: 48,
        borderRadius: isActive ? 16 : 24,
        background: isActive ? 'var(--primary)' : 'var(--bg-tertiary)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        cursor: 'pointer',
        transition: 'all 0.3s var(--ease-out-cubic)',
        position: 'relative',
        boxShadow: isActive ? 'var(--shadow-glow)' : 'none',
      }}
      title="R1Gate - Friends & Messages"
    >
      {/* R1Gate Logo - Triangle of circles */}
      <svg
        width="28"
        height="28"
        viewBox="0 0 100 100"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        {/* Three circles forming a triangle */}
        <circle
          cx="30"
          cy="35"
          r="10"
          fill={isActive ? '#0D0D0D' : 'var(--primary)'}
        />
        <circle
          cx="70"
          cy="35"
          r="10"
          fill={isActive ? '#0D0D0D' : 'var(--primary)'}
        />
        <circle
          cx="50"
          cy="70"
          r="10"
          fill={isActive ? '#0D0D0D' : 'var(--primary)'}
        />
        {/* Connecting lines */}
        <line
          x1="30"
          y1="35"
          x2="70"
          y2="35"
          stroke={isActive ? '#0D0D0D' : 'var(--primary)'}
          strokeWidth="4"
        />
        <line
          x1="30"
          y1="35"
          x2="50"
          y2="70"
          stroke={isActive ? '#0D0D0D' : 'var(--primary)'}
          strokeWidth="4"
        />
        <line
          x1="70"
          y1="35"
          x2="50"
          y2="70"
          stroke={isActive ? '#0D0D0D' : 'var(--primary)'}
          strokeWidth="4"
        />
      </svg>

      {/* Pending requests badge */}
      {pendingRequests > 0 && (
        <div
          style={{
            position: 'absolute',
            top: -4,
            right: -4,
            minWidth: 18,
            height: 18,
            borderRadius: 9,
            background: 'var(--danger)',
            color: 'white',
            fontSize: 11,
            fontWeight: 600,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            padding: '0 5px',
            boxShadow: '0 2px 4px rgba(0,0,0,0.3)',
          }}
        >
          {pendingRequests > 99 ? '99+' : pendingRequests}
        </div>
      )}
    </div>
  );
};

export default ConnectLogo;
