/**
 * RNNoise-based noise suppression service using AudioWorklet
 *
 * Uses @sapphi-red/web-noise-suppressor which provides:
 * - AudioWorklet (runs in separate audio thread, no glitches)
 * - RNNoise WASM with SIMD optimization
 * - NoiseGate for cutting silence/breathing
 */

import { RnnoiseWorkletNode, NoiseGateWorkletNode, loadRnnoise } from '@sapphi-red/web-noise-suppressor';

export type NoiseSuppressionLevel = 'off' | 'low' | 'medium' | 'high';

// Level configurations with noise gate thresholds
// Lower values = more sensitive (quieter sounds pass through)
const LEVEL_CONFIGS: Record<NoiseSuppressionLevel, {
  enabled: boolean;
  gateOpenThreshold: number;  // dB level to open gate (let audio through)
  gateCloseThreshold: number; // dB level to close gate (mute)
  holdMs: number;             // how long to keep gate open after speech ends
  gain: number;               // output gain multiplier
}> = {
  off: { enabled: false, gateOpenThreshold: -60, gateCloseThreshold: -65, holdMs: 100, gain: 1.0 },
  low: { enabled: true, gateOpenThreshold: -55, gateCloseThreshold: -60, holdMs: 300, gain: 1.0 },
  medium: { enabled: true, gateOpenThreshold: -50, gateCloseThreshold: -55, holdMs: 250, gain: 1.0 },
  high: { enabled: true, gateOpenThreshold: -45, gateCloseThreshold: -50, holdMs: 200, gain: 1.0 },
};

class NoiseSuppression {
  private audioContext: AudioContext | null = null;
  private sourceNode: MediaStreamAudioSourceNode | null = null;
  private destinationNode: MediaStreamAudioDestinationNode | null = null;
  private rnnoiseNode: RnnoiseWorkletNode | null = null;
  private noiseGateNode: NoiseGateWorkletNode | null = null;
  private gainNode: GainNode | null = null;
  private wasmBinary: ArrayBuffer | null = null;

  private rnnoiseInitialized = false;
  private noiseGateInitialized = false;
  private isEnabled = true;
  private level: NoiseSuppressionLevel = 'medium';

  /**
   * Load RNNoise WASM binary
   */
  private async loadWasmBinary(): Promise<ArrayBuffer> {
    if (this.wasmBinary) {
      return this.wasmBinary;
    }

    try {
      console.log('[NoiseSuppression] Loading RNNoise WASM...');

      this.wasmBinary = await loadRnnoise({
        url: '/rnnoise/rnnoise.wasm',
        simdUrl: '/rnnoise/rnnoise_simd.wasm',
      });

      console.log('[NoiseSuppression] WASM loaded successfully');
      return this.wasmBinary;
    } catch (error) {
      console.error('[NoiseSuppression] Failed to load WASM:', error);
      throw error;
    }
  }

  /**
   * Initialize AudioWorklet modules
   */
  private async initializeWorklets(ctx: AudioContext): Promise<void> {
    try {
      // Initialize RNNoise worklet
      if (!this.rnnoiseInitialized) {
        console.log('[NoiseSuppression] Adding RNNoise AudioWorklet module...');
        await ctx.audioWorklet.addModule('/rnnoise/workletProcessor.js');
        this.rnnoiseInitialized = true;
        console.log('[NoiseSuppression] RNNoise AudioWorklet initialized');
      }

      // Initialize NoiseGate worklet
      if (!this.noiseGateInitialized) {
        console.log('[NoiseSuppression] Adding NoiseGate AudioWorklet module...');
        await ctx.audioWorklet.addModule('/rnnoise/noiseGateWorklet.js');
        this.noiseGateInitialized = true;
        console.log('[NoiseSuppression] NoiseGate AudioWorklet initialized');
      }
    } catch (error) {
      console.error('[NoiseSuppression] Failed to initialize AudioWorklet:', error);
      throw error;
    }
  }

  /**
   * Process an audio stream through RNNoise + NoiseGate
   * Returns a new MediaStream with noise-suppressed audio
   */
  async processStream(inputStream: MediaStream): Promise<MediaStream> {
    const config = LEVEL_CONFIGS[this.level];
    if (!config.enabled) {
      console.log('[NoiseSuppression] Disabled, returning original stream');
      return inputStream;
    }

    try {
      // Clean up previous resources if any
      this.destroy();

      // Load WASM binary
      const wasmBinary = await this.loadWasmBinary();

      // Create audio context at 48kHz (RNNoise requirement)
      this.audioContext = new AudioContext({ sampleRate: 48000 });

      // Resume context if suspended (Chrome autoplay policy)
      if (this.audioContext.state === 'suspended') {
        await this.audioContext.resume();
      }

      // Initialize worklets
      await this.initializeWorklets(this.audioContext);

      // Create source from input stream
      this.sourceNode = this.audioContext.createMediaStreamSource(inputStream);

      // Create RNNoise worklet node (removes background noise)
      this.rnnoiseNode = new RnnoiseWorkletNode(this.audioContext, {
        wasmBinary,
        maxChannels: 1,
      });

      // Create NoiseGate worklet node (cuts silence/breathing)
      this.noiseGateNode = new NoiseGateWorkletNode(this.audioContext, {
        openThreshold: config.gateOpenThreshold,
        closeThreshold: config.gateCloseThreshold,
        holdMs: config.holdMs,
        maxChannels: 1,
      });

      // Create gain node to boost volume after processing
      this.gainNode = this.audioContext.createGain();
      this.gainNode.gain.value = config.gain;

      // Create destination for output stream
      this.destinationNode = this.audioContext.createMediaStreamDestination();

      // Connect: source -> rnnoise -> noiseGate -> gain -> destination
      this.sourceNode.connect(this.rnnoiseNode);
      this.rnnoiseNode.connect(this.noiseGateNode);
      this.noiseGateNode.connect(this.gainNode);
      this.gainNode.connect(this.destinationNode);

      console.log(`[NoiseSuppression] Pipeline created (level: ${this.level}, gate: ${config.gateOpenThreshold}/${config.gateCloseThreshold}dB, gain: ${config.gain})`);
      return this.destinationNode.stream;

    } catch (error) {
      console.error('[NoiseSuppression] Failed to create pipeline:', error);
      this.destroy();
      return inputStream;
    }
  }

  /**
   * Set noise suppression level
   */
  setLevel(level: NoiseSuppressionLevel): void {
    this.level = level;
    const config = LEVEL_CONFIGS[level];
    this.isEnabled = config.enabled;
    localStorage.setItem('noiseSuppressionLevel', level);
    console.log(`[NoiseSuppression] Level set to: ${level}`);
  }

  /**
   * Get current noise suppression level
   */
  getLevel(): NoiseSuppressionLevel {
    return this.level;
  }

  /**
   * Check if noise suppression is enabled
   */
  isNoiseSuppressionEnabled(): boolean {
    const enabled = this.isEnabled && this.level !== 'off';
    console.log(`[NoiseSuppression] isNoiseSuppressionEnabled: ${enabled} (isEnabled=${this.isEnabled}, level=${this.level})`);
    return enabled;
  }

  /**
   * Clean up resources
   */
  destroy(): void {
    if (this.gainNode) {
      this.gainNode.disconnect();
      this.gainNode = null;
    }
    if (this.noiseGateNode) {
      this.noiseGateNode.disconnect();
      this.noiseGateNode = null;
    }
    if (this.rnnoiseNode) {
      this.rnnoiseNode.destroy();
      this.rnnoiseNode.disconnect();
      this.rnnoiseNode = null;
    }
    if (this.sourceNode) {
      this.sourceNode.disconnect();
      this.sourceNode = null;
    }
    if (this.destinationNode) {
      this.destinationNode = null;
    }
    if (this.audioContext) {
      this.audioContext.close();
      this.audioContext = null;
    }
    // Reset initialization flags so worklets are re-added for new context
    this.rnnoiseInitialized = false;
    this.noiseGateInitialized = false;
    console.log('[NoiseSuppression] Resources cleaned up');
  }

  /**
   * Load saved settings from localStorage
   */
  loadSettings(): void {
    const savedLevel = localStorage.getItem('noiseSuppressionLevel') as NoiseSuppressionLevel | null;
    console.log(`[NoiseSuppression] loadSettings: savedLevel=${savedLevel}, current level=${this.level}, isEnabled=${this.isEnabled}`);
    if (savedLevel && LEVEL_CONFIGS[savedLevel]) {
      this.setLevel(savedLevel);
    } else {
      console.log(`[NoiseSuppression] No saved level, using default: ${this.level}`);
    }
  }
}

// Export singleton instance
export const noiseSuppression = new NoiseSuppression();

// Export class for testing
export { NoiseSuppression };
