import React, { useState, useEffect } from 'react';
import '../types/electron.d';

interface TitleBarProps {
  title: string;
}

const TitleBar: React.FC<TitleBarProps> = ({ title }) => {
  const [isMaximized, setIsMaximized] = useState(false);
  const [hoveredButton, setHoveredButton] = useState<string | null>(null);
  const isElectron = !!window.electronAPI?.isElectron;

  // Check initial maximized state and listen for changes
  useEffect(() => {
    if (!isElectron) return;

    const checkMaximized = async () => {
      if (window.electronAPI?.windowIsMaximized) {
        const maximized = await window.electronAPI.windowIsMaximized();
        setIsMaximized(maximized);
      }
    };

    checkMaximized();

    // Listen for maximize/unmaximize events
    const cleanup = window.electronAPI?.onWindowMaximized?.((maximized) => {
      setIsMaximized(maximized);
    });

    return () => {
      cleanup?.();
    };
  }, [isElectron]);

  // Only render in Electron
  if (!isElectron) {
    return null;
  }

  const handleMinimize = () => {
    window.electronAPI?.windowMinimize();
  };

  const handleMaximize = () => {
    window.electronAPI?.windowMaximize();
  };

  const handleClose = () => {
    window.electronAPI?.windowClose();
  };

  return (
    <div style={styles.titleBar}>
      <div style={styles.dragRegion} />
      <div style={styles.title}>{title}</div>
      <div style={styles.windowControls}>
        {/* Minimize */}
        <button
          style={{
            ...styles.controlButton,
            backgroundColor: hoveredButton === 'minimize' ? 'rgba(255, 255, 255, 0.1)' : 'transparent',
          }}
          onClick={handleMinimize}
          onMouseEnter={() => setHoveredButton('minimize')}
          onMouseLeave={() => setHoveredButton(null)}
          title="Minimize"
        >
          <svg width="10" height="1" viewBox="0 0 10 1">
            <rect width="10" height="1" fill="currentColor" />
          </svg>
        </button>

        {/* Maximize/Restore */}
        <button
          style={{
            ...styles.controlButton,
            backgroundColor: hoveredButton === 'maximize' ? 'rgba(255, 255, 255, 0.1)' : 'transparent',
          }}
          onClick={handleMaximize}
          onMouseEnter={() => setHoveredButton('maximize')}
          onMouseLeave={() => setHoveredButton(null)}
          title={isMaximized ? 'Restore' : 'Maximize'}
        >
          {isMaximized ? (
            // Restore icon (two overlapping squares)
            <svg width="10" height="10" viewBox="0 0 10 10">
              <path
                fill="none"
                stroke="currentColor"
                strokeWidth="1"
                d="M2,3 L2,9 L8,9 L8,3 L2,3 M3,3 L3,1 L9,1 L9,7 L8,7"
              />
            </svg>
          ) : (
            // Maximize icon (single square)
            <svg width="10" height="10" viewBox="0 0 10 10">
              <rect
                width="9"
                height="9"
                x="0.5"
                y="0.5"
                fill="none"
                stroke="currentColor"
                strokeWidth="1"
              />
            </svg>
          )}
        </button>

        {/* Close */}
        <button
          style={{
            ...styles.controlButton,
            ...styles.closeButton,
            backgroundColor: hoveredButton === 'close' ? '#e81123' : 'transparent',
          }}
          onClick={handleClose}
          onMouseEnter={() => setHoveredButton('close')}
          onMouseLeave={() => setHoveredButton(null)}
          title="Close"
        >
          <svg width="10" height="10" viewBox="0 0 10 10">
            <path
              fill="currentColor"
              d="M1,0 L5,4 L9,0 L10,1 L6,5 L10,9 L9,10 L5,6 L1,10 L0,9 L4,5 L0,1 Z"
            />
          </svg>
        </button>
      </div>
    </div>
  );
};

const styles: { [key: string]: React.CSSProperties } = {
  titleBar: {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    height: '32px',
    backgroundColor: '#0D1012',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 9999,
    userSelect: 'none',
  },
  dragRegion: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    WebkitAppRegion: 'drag',
  } as React.CSSProperties,
  title: {
    fontSize: '12px',
    fontWeight: 500,
    color: 'var(--text-secondary)',
    letterSpacing: '0.5px',
    pointerEvents: 'none',
  },
  windowControls: {
    position: 'absolute',
    right: 0,
    top: 0,
    display: 'flex',
    height: '32px',
    WebkitAppRegion: 'no-drag',
  } as React.CSSProperties,
  controlButton: {
    width: '46px',
    height: '32px',
    border: 'none',
    background: 'transparent',
    color: 'var(--text-secondary)',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    cursor: 'pointer',
    transition: 'background-color 0.1s',
  },
  closeButton: {
    // Close button hover color handled inline
  },
};

export default TitleBar;
